.markdown-previewer {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  line-height: 1.5;
  position: relative;
  margin: 0;
  padding: 0;
  background: #171c1c;
  color: #f0f4f4; }
  .markdown-previewer a {
    color: #1ae680;
    text-decoration: none;
    font-weight: 600; }
    .markdown-previewer a:visited {
      color: #14b866; }
    .markdown-previewer a:hover {
      color: #75f0b3; }
    .markdown-previewer a:active {
      color: #8866cc; }
  .markdown-previewer code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
    display: block;
    padding: 1em;
    background: #222a2a;
    border-left: solid 0.25em #14b866;
    border-radius: .75em;
    margin: 1.5em;
    overflow: auto; }
  .markdown-previewer blockquote {
    display: block;
    padding: 1em;
    background: #222a2a;
    border-left: solid 0.25em #6a40bf;
    border-radius: .75em;
    margin: 1.5em;
    overflow: auto; }
  .markdown-previewer hr {
    border-color: #6a40bf; }
  .markdown-previewer table {
    margin: 0;
    padding: 0;
    border: none;
    border-collapse: collapse;
    max-width: 100%;
    overflow: auto; }
  .markdown-previewer th {
    background-color: #553399;
    margin: 0;
    padding: 1em;
    border: none; }
  .markdown-previewer tr {
    background-color: #222a2a;
    border: none;
    margin: 0;
    padding: 1em;
    max-width: 100%;
    overflow: auto; }
    .markdown-previewer tr:nth-child(odd) {
      background-color: #0b0e0e; }
    .markdown-previewer tr:hover {
      background-color: #0f8a4d; }
  .markdown-previewer td {
    border: none;
    margin: 0;
    padding: 1em; }
  .markdown-previewer nav {
    position: relative;
    width: 100%;
    padding: 1.25rem 2rem;
    box-sizing: border-box;
    background: #543299;
    margin-bottom: 1em; }
    .markdown-previewer nav h1 {
      padding: 0;
      margin: 0; }
  .markdown-previewer textarea {
    position: relative;
    color: #f0f4f4;
    background: #222a2a;
    font-size: 1.2em;
    line-height: 1.5;
    width: 45%;
    height: 150vh;
    min-height: 350px;
    padding: 1em;
    box-sizing: border-box;
    float: left;
    border-radius: .75em;
    border: none;
    border-left: solid 0.25em #394646;
    border-right: solid 0.25em #394646; }
    .markdown-previewer textarea:focus {
      outline: none;
      border-color: #738c8c; }
  .markdown-previewer img {
    max-width: 100%; }

.code-span {
  position: relative;
  display: inline-block;
  padding: .25em;
  margin: 0;
  top: .6em; }

.enterMD {
  position: relative;
  padding: 0 2em;
  width: 45%;
  float: left;
  box-sizing: border-box; }

.previewer-top {
  position: relative;
  padding: 0 2em;
  width: 55%;
  float: left;
  box-sizing: border-box; }

.previewer-bottom {
  position: relative;
  padding: 0 2em;
  box-sizing: border-box;
  opacity: 0;
  height: 0;
  margin: 0; }

.markdown-wrapper {
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  max-width: 100%;
  overflow: auto; }

.editor-wrapper {
  position: relative;
  padding: 1em;
  height: 100%;
  box-sizing: border-box; }

#preview {
  position: relative;
  width: 54%;
  height: auto;
  min-height: 500px;
  padding: 1em 2em;
  box-sizing: border-box;
  float: left;
  margin: 0; }

@media (max-width: 1260px) {
  .enterMD {
    width: 100%;
    margin: 0; }
  .editor-wrapper {
    margin: 0; }
  .markdown-previewer textarea {
    float: none;
    width: 100%;
    height: 50vh; }
  .previewer-top {
    opacity: 0;
    height: 0;
    width: 0; }
  .previewer-bottom {
    opacity: 1;
    height: auto; }
  #preview {
    float: none;
    width: 100%; } }

@media (max-width: 800px) {
  .enterMD {
    padding: 0 1em; }
  .editor-wrapper {
    margin: 0;
    padding: 0; }
  .previewer-bottom {
    padding: 0 1em; }
  #preview {
    padding: 1em; }
  .markdown-previewer code {
    margin: 0; }
  .markdown-previewer blockquote {
    margin: 0; } }
