.markdown-previewer {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    line-height: 1.5;
    position: relative;
    margin: 0;
    padding: 0;
    background: hsl(180, 10%, 10%);
    color: hsl(180, 15%, 95%);


    a {
        color: hsl(150, 80%, 50%);
        text-decoration: none;
        font-weight: 600;
        
        &:visited {
            color: hsl(150, 80%, 40%);
        }
        
        &:hover {
            color: hsl(150, 80%, 70%);
        }

        &:active {
            color: hsl(260, 50%, 60%);
        }
    }

    code {
        font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
        display: block;
        padding: 1em;
        background: hsl(180, 10%, 15%);
        border-left: solid .25em hsl(150, 80%, 40%);
        // border-right: solid .25em hsl(150, 80%, 40%);
        border-radius: .75em;
        margin: 1.5em;
        overflow: auto;
    }

    blockquote {
        display: block;
        padding: 1em;
        background: hsl(180, 10%, 15%);
        border-left: solid .25em hsl(260, 50%, 50%);
        // border-right: solid .25em hsl(260, 50%, 50%);
        border-radius: .75em;
        margin: 1.5em;
        overflow: auto;
    }

    hr {
        border-color: hsl(260, 50%, 50%);
    }

    table {
        // background: hsl(180, 10%, 15%);
        margin: 0;
        padding: 0;
        border: none;
        border-collapse: collapse;
        max-width: 100%;
        overflow: auto;
    }

    th {
        background-color: hsl(260, 50%, 40%);
        margin: 0;
        padding: 1em;
        border: none;
    }

    tr {
        background-color: hsl(180, 10%, 15%);
        border: none;
        margin: 0;
        padding: 1em;
        max-width: 100%;
        overflow: auto;

        &:nth-child(odd) {
            background-color: hsl(180, 10%, 5%);
        }

        &:hover {
            background-color: hsl(150, 80%, 30%);
        }
    }

    td {
        border: none;
        margin: 0;
        padding: 1em;
    }

    nav {
        position: relative;
        width: 100%;
        padding: 1.25rem 2rem;
        box-sizing: border-box;
        background: #543299;
        margin-bottom: 1em;

        h1 {
            padding: 0;
            margin: 0;
        }
    }

    textarea {
        position: relative;
        color: hsl(180, 15%, 95%);
        background: hsl(180, 10%, 15%);
        font-size: 1.2em;
        line-height: 1.5;
        width: 45%;
        height: 150vh;
        min-height: 350px;
        padding: 1em;
        box-sizing: border-box;
        float: left;
        border-radius: .75em;
        border: none;
        border-left: solid .25em hsl(180, 10%, 25%);
        border-right: solid .25em hsl(180, 10%, 25%);
    
        &:focus {
            outline: none;
            border-color: hsl(180, 10%, 50%);
        }
    }
    
    img {
        max-width: 100%;
    }

}

// reduced padding and removed margin for in-line code elements
.code-span {
    position: relative;
    display: inline-block;
    padding: .25em;
    margin: 0;
    top: .6em;
}

.enterMD {
    position: relative;
    padding: 0 2em;
    width: 45%;
    float: left;
    box-sizing: border-box;
}

.previewer-top {
    position: relative;
    padding: 0 2em;
    width: 55%;
    float: left;
    box-sizing: border-box;
}

.previewer-bottom {
    position: relative;
    padding: 0 2em;
    box-sizing: border-box;
    opacity: 0;
    height: 0;
    margin: 0;
}

.markdown-wrapper {
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    max-width: 100%;
    overflow: auto;
}

.editor-wrapper {
    position: relative;
    padding: 1em;
    height: 100%;
    box-sizing: border-box;
}

#preview {
    position: relative;
    width: 54%;
    height: auto;
    min-height: 500px;
    padding: 1em 2em;
    box-sizing: border-box;
    float: left;
    margin: 0;
}


@media (max-width: 1260px) {
    .enterMD {
        width: 100%;
        margin: 0;
    }

    .editor-wrapper {
        margin: 0;
    }

    .markdown-previewer textarea {
        float: none;
        width: 100%;
        height: 50vh;
    }

    .previewer-top {
        opacity: 0;
        height: 0;
        width: 0;
    }

    .previewer-bottom {
        opacity: 1;
        height: auto;
    }

    #preview {
        float: none;
        width: 100%;
    }

}

@media (max-width: 800px) {

    .enterMD {
        padding: 0 1em;
    }

    .editor-wrapper {
        margin: 0;
        padding: 0;
    }

    .previewer-bottom {
        padding: 0 1em;
    }

    #preview {
        padding: 1em;
    }

    .markdown-previewer code {
        margin: 0;
    }

    .markdown-previewer blockquote {
        margin: 0;
    }
}