.quote-container {
    position: relative;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(150deg, #1CB5E0 0%, #000851 100%);
}

#quote-box {
    position: relative;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    width: 500px;
    height: auto;
    background-color: whitesmoke;
    color: black;
    // margin: 20vh auto;
    padding: 3em;
    box-sizing: border-box;
    border-radius: 1em;
    box-shadow: .5em .5em 1em rgba(0, 0, 0, .3);

    #text {
        position: relative;
        margin-bottom: 6rem;
        font-size: 1.2em;
    }

    #author {
        position: absolute;
        right: 3rem;
        bottom: 4.5rem;
    }

    .button {
        padding: 1.25em;
        text-align: center;
        width: 50%;
        background-color: rgba(29,161,242,1.00);
        color: white;
        box-sizing: border-box;
        cursor: pointer;

        &:focus,
        &:hover {
            outline: none;
            background-color: rgb(78, 186, 253);
        }
    }

    #new-quote {
        @extend .button;
        position: absolute;
        right: 0;
        bottom: 0;
        border: none;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
        font-size: 1em;
        border-left: 1px solid rgb(31, 137, 202);
        border-bottom-right-radius: 1em;
    }

    #tweet-quote {
        @extend .button;
        position: absolute;
        left: 0;
        bottom: 0;
        border-right: 1px solid rgb(31, 137, 202);
        border-bottom-left-radius: 1em;
        text-decoration: none;
    }

}

@media (max-width: 500px) {
    #quote-box {
        width: 100%;
        border-radius: 0;

        #new-quote {
            border-bottom-right-radius: 0;
        }
    
        #tweet-quote {
            border-bottom-left-radius: 0;
        }
    }

}