.pomodoro-container {
    position: relative;
    margin: 0;
    padding: 0;
    font-size: 1.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

// Remove clickability of Font Awsome SVG so that they don't interfere with returning the proper element id of the buttons.
.fas {
    pointer-events: none;
}

.pomodoro {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pom-button {
    background: hsl(155, 80%, 45%);
    width: 2em;
    height: 2em;
    border-radius: .5em;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        background: hsl(155, 100%, 60%);
    }
}

.main-display {
    background: hsl(155, 40%, 40%);
    padding: 2em;
    border-radius: 1em;
    text-align: center;

    .main-label {
        font-size: 1.75em;
        font-weight: 700;
        margin-bottom: .5em;
    }

    .session-break-label {
        font-size: 1.25em;
        font-weight: 600;
    }
    
    .main-timer {
        font-size: 3em;
    }

    .main-buttons {
        margin-top: 1em;
        display: flex;
        justify-content: space-around;
    }

    .pom-button {
        width: 2.5em;
        height: 2.5em;
    }
}

.flank-display {
    background: hsl(155, 40%, 25%);
    padding: 2em;
    text-align: center;

    .flank-label {
        font-size: 1.25em;
        font-weight: 600;
        margin-bottom: .7em;
    }

    .flank-items {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    
    .flank-timer {
        font-size: 2em;
    }
}

.session-settings {
    border-radius: 1em 0 0 1em;
}

.break-settings {
    border-radius: 0 1em 1em 0;
}


@media(max-width: 1000px) {
    .pomodoro-container {
        font-size: 1em;
    }
}

@media(max-width: 666px) {
    .pomodoro {
        flex-direction: column;
    }

    .session-settings {
        border-radius: 1em 1em 0 0;
    }

    .break-settings {
        border-radius: 0 0 1em 1em;
    }
}