.calc-wrapper {
    position: relative;
    font-size: 1.5em;
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.calc-container {
    width: 12em;
    // margin: 300px auto 0 auto;
}

.calc-display {
    // width: 12em;
    position: relative;
    right: 0;
    height: 3em;
    background: hsl(180, 10%, 5%);
    border: 1px solid blue;
    padding: .7em 1.2em;
    text-align: right;
    box-sizing: border-box;
    overflow-x: auto;
    overflow-y: hidden;
}

.button-layout {
    // position: relative;
    display: grid;
    grid-template-columns: repeat(4, 3em);
    grid-template-rows: repeat(5, 3em);
    width: 12em;
}

.calc-button {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: stretch;
    align-self: stretch;
    background: hsl(180, 10%, 5%);
    border: 1px solid blue;
    // padding: .7em;
    text-align: center;
    // width: 3em;
    // height: 3em;
    margin: 0;
    box-sizing: border-box;
    cursor: pointer;

    &:hover {
        background: blue;
    }
}

#zero {
    grid-column: 1 / 3;
    grid-row: 5;
}

#one {
    grid-column: 1;
    grid-row: 4;
}

#two {
    grid-column: 2;
    grid-row: 4;
}

#three {
    grid-column: 3;
    grid-row: 4;
}

#four {
    grid-column: 1;
    grid-row: 3;
}

#five {
    grid-column: 2;
    grid-row: 3;
}

#six {
    grid-column: 3;
    grid-row: 3;
}

#seven {
    grid-column: 1;
    grid-row: 2;
}

#eight {
    grid-column: 2;
    grid-row: 2;
}

#nine {
    grid-column: 3;
    grid-row: 2;
}

#decimal {
    grid-column: 3;
    grid-row: 5;
}

#add {
    grid-column: 4;
    grid-row: 2 / 4;
}

#subtract {
    grid-column: 4;
    grid-row: 1;
}

#multiply {
    grid-column: 3;
    grid-row: 1;
}

#divide {
    grid-column: 2;
    grid-row: 1;
}

#equals {
    grid-column: 4;
    grid-row: 4 / 6;
}

#clear {
    grid-column: 1;
    grid-row: 1;
}
