// Variables
$bg-secondary: hsl(188, 15%, 12%);
$bg-secondary-highlight: hsl(188, 15%, 25%);

$bg-primary: hsl(188, 16%, 6%);
$bg-primary-highlight: hsl(188, 16%, 15%);

$text-color: hsl(188, 15%, 97%);

$primary: hsl(188, 76%, 20%);
$primary-gradient: linear-gradient(0deg, hsl(188, 76%, 20%) 0%, hsl(188, 76%, 25%) 100%);
$primary-highlight: hsl(188, 75%, 40%);

$link-highlight: hsl(188, 100%, 70%);

html {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    margin: 0;
    padding: 0;
    background: $bg-primary;
    color: $text-color;
}

.home-container {
    position: relative;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
    
    font-family: "Exo 2";
    line-height: 1.25;
}

.nav-box {
    // width: 80%;
    background-color: $bg-secondary;
    box-sizing: border-box;
    box-shadow: .5rem .5rem 1rem rgba(0, 0, 0, 0.5);
    // margin-top: -5rem;
}

.nav-header {
    position: relative;
    width: 100%;
    margin: 0;
    padding: 3rem;
    text-align: left;
    box-sizing: border-box;
    background-image: $primary-gradient;
    overflow: hidden;

    h1 {
        margin: 0;
    }

    p {
        margin: 1rem 0 0 0;
        max-width: 49rem;
        font-size: 1.1rem;
    }

    a {
        color: $text-color;
        font-weight: 600;
        // text-decoration: none;

        &:hover {
            color: $link-highlight;
        }
    }
}

.react-logo {
    position: absolute;
    width: 50%;
    right: -8%;
    top: -10%;
    opacity: .08;
    animation-name: logo-spin;
    animation-duration: 20s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    pointer-events: none;
}

@keyframes logo-spin {
    0%  {transform: rotate(0deg);}
    100%    {transform: rotate(360deg);}
}

.home-link-ul {
    position: relative;
    list-style: none;
    padding: 3rem;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    box-sizing: border-box;
    font-size: 1.3em;
    // background-color: $bg-secondary;

    li {
        position: relative;
        // float: left;
        margin: 1em;
    }
}

.home-link {
    color: white;
    text-decoration: none;

    &:hover {
        color: $link-highlight;
        // text-decoration: underline;
    }
}
